:root {
  --font-body: "Lato", sans-serif;
  --font-stat: "Stalemate", cursive;
}

:root {
  --white: hsl(0, 0%, 100%);
  --whiteLight: hsl(0, 8%, 75%);
  --black: hsl(0, 0%, 0%);
  --violet: hsl(203, 84%, 17%);
  --gray: hsl(0, 0%, 53%);
  --grayLight: hsla(0, 0%, 91%, 1);
  --grayDark: hsla(0, 0%, 89%, 1);
  --pink: #cc5500;
  --brown:hsla(25, 100%, 40%, 1);
  --darkLight: hsla(0, 4%, 10%, 0.7);
  --colorBlue: hsl(202, 25%, 94%);
  --pinkLink:hsla(7, 28%, 48%, .12);
  --rose:#f07667fc;
  --secondarycolor:rgba(27, 12, 1, 0.7);
  --tertiarycolor: #f67b23;
  --rosefair:hsl(21, 63%, 89%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  background-color: var(--pinkLink);
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: ) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.containers,
.conteneur {
  padding: 0 8.75rem;
  max-width: 87.5rem;
  margin: 0 auto;
}
@media (max-width: 59.375em) {
  .containers,
.conteneur {
    padding: 0 3.125rem;
  }
}
@media (max-width: 39.9375em) {
  .containers,
.conteneur {
    padding: 0 1.25rem;
  }
}

.sectionfaq {
  padding-top: 3rem;
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}
.sectionfaq__description {
  text-align: center;
}
.sectionfaq__container {
  display: grid;
  width: 100%;
  gap: 2rem;
}
.sectionfaq__content {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 1.5rem 1rem;
}
.sectionfaq__question {
  font-size: 1.3rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}
.sectionfaq__reponse {
  padding: 1.5rem 0 0 1.5rem;
  flex-direction: column;
  gap: 1rem;
}
.sectionfaq .faq__close .sectionfaq__reponse {
  height: 0;
  overflow: hidden;
  display: none;
}
.sectionfaq .faq__close .icon--remove {
  display: none;
}
.sectionfaq .faq__open .sectionfaq__reponse {
  height: 0;
  height: max-content;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.sectionfaq .faq__open .icon--add {
  display: none;
}
.sectionfaq .faq__open {
  border-top: 0.2rem solid var(--pink);
}

