.banner.show {
  z-index: 500;
  width: 100%;
  height: 100vh;
}
.banner.show .banner__image.show {
  gap: 0;
  overflow: hidden;
  background: url(https://res.cloudinary.com/digital-x-sarl/image/upload/v1669820964/mariage%20client%201/banner/bannerback.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  width: 100%;
  height: 100vh;
  animation: fadein 2s;
}
@keyframes fadein {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
@media (max-width: 59.375em) {
  .banner.show .banner__image.show {
    background: url(https://res.cloudinary.com/digital-x-sarl/image/upload/v1670254865/mariage%20client%201/banner/bannerbacktablet.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (max-width: 39.9375em) {
  .banner.show .banner__image.show {
    background: url(https://res.cloudinary.com/digital-x-sarl/image/upload/v1669986329/mariage%20client%201/banner/bannerbackmobile.png);
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
  }
}
.banner.show .show > div {
  opacity: 1;
}
.banner.show .banner__playbtn {
  display: none;
}
.banner.show .banner__text {
  display: block;
  position: absolute;
  top: 60%;
  transform: translate(-50%, -50%);
  color: #fff;
  left: 50%;
  font-family: "STIX Two Text";
  font-style: normal;
  text-align: center;
  width: 100%;
}
.banner.show .banner__text :nth-child(1) {
  font-weight: 400;
  font-size: 1.75rem;
  letter-spacing: 0.26em;
}
@media (max-width: 59.375em) {
